import * as emailjs from 'emailjs-com';
import { Button, Control, Field, Input, Label, Textarea } from 'rbx';
import React, { Component } from "react";
import Swal from 'sweetalert2';

var Recaptcha = require('react-recaptcha');

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      subject: '',
      message: '',
      recaptchaLoad: false,
      isVerified: false,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.resetForm = this.resetForm.bind(this);
    this.handleChange = this.handleChange.bind(this);


    /* and this... */
    this.recaptchaLoaded = this.recaptchaLoaded.bind(this);
    this.verifiedRecaptcha = this.verifiedRecaptcha.bind(this);
  }

  handleSubmit(event) {
    const { recaptchaLoad, isVerified } = this.state;
    event.preventDefault();
    if (recaptchaLoad && isVerified) {
    const { name, email, subject, message } = this.state;
    const templateParams = {
      from_name: name,
      from_email: email,
      to_name: 'Facundo',
      subject,
      message_html: message,
    };
    emailjs.send(
      'service_y7m1pt7',
      'template_vxv9cji',
       templateParams,
      'user_NAkdQArsUzbw24YZTtpV1'
    )
    Swal.fire('Congrats!', `
    Your message has been sent successfully. We will contact you soon<br>        
              `);
    this.resetForm();
  } else {
    Swal.fire('Oops...', `
    Please check the Recaptcha before sending your message<br>        
              `);
  }
    this.resetForm();
  };

  resetForm() {
    this.setState({
      name: '',
      email: '',
      subject: '',
      message: '',
      recaptchaLoad: false,
      isVerified: false,
    });
  }

  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  recaptchaLoaded() {
    this.setState({ recaptchaLoad: true });
  }
  
  verifiedRecaptcha(response) {
    if (response) {
      this.setState({ isVerified: true });
    }
  }

  render() {
    const { name, email, subject, message } = this.state;

    return (
      <section id="contact">
        <div className="row section-head">
          <div className="two columns header-col">
            <h1>
              <span>Contact Me</span>
            </h1>
          </div>
        </div>
        <div className="row">
          <div className="eight columns">
      <form onSubmit={this.handleSubmit}>
        <Field>
          <Label>Name</Label>
          <Control>
            <Input
              name="name"
              type="text"
              placeholder="Your first and last name"
              value={name}
              onChange={this.handleChange}
              />
          </Control>
        </Field>
        <Field>
          <Label>Email for contact</Label>
          <Control>
            <Input
              name="email"
              type="email"
              placeholder="email@gmail.com"
              value={email}
              onChange={this.handleChange}
              />
          </Control>
        </Field>
        <Field>
          <Label>Subject</Label>
          <Control>
            <Input
              name="subject"
              type="text"
              placeholder="What is the subject?"
              value={subject}
              onChange={this.handleChange}
              />
          </Control>
        </Field>
        <Field>
          <Label>Message</Label>
          <Control>
            <Textarea
              name="message"
              placeholder="Tell me more about..."
              value={message}
              onChange={this.handleChange}
              />
          <Recaptcha
          sitekey="6LcsBpAaAAAAAFUIOcc3WvZPox3Ebphqeb3vBDXl"
          render="explicit"
          onloadCallback={this.recaptchaLoaded}
          verifyCallback={this.verifiedRecaptcha}
          />
          </Control>
        </Field>
        <br></br>
        <Field kind="group">
          <Control>
            <Button className="submit">Send</Button>
          </Control>
        </Field>
      </form>
      <div id="message-warning"> Error boy</div>
            <div id="message-success">
              <i className="fa fa-check"></i>Your message was sent, thank you!
              <br />
            </div>
          </div>
          <aside className="four columns footer-widgets">
            <div className="widget widget_contact">
              <h4>Get in touch</h4>
              <p className="address">
                üá¶üá∑  GMT -3 <br />
                üè° Bah√≠a Blanca, Buenos Aires, Argentina
                <br />
              </p>
            </div>
          </aside>
        </div>
      </section>
    );
  }
}

export default Contact;
